<template>
  <div class="contentarea">
    <h1>MDN - WebRTC: Still photo capture demo</h1>
    <p>
      This example demonstrates how to set up a media stream using your built-in
      webcam, fetch an image from that stream, and create a PNG using that
      image.
    </p>
    <div class="camera">
      <video id="video" ref="video">Video stream not available.</video>
      <button v-on:click="startScan">startScan</button>
      <button v-on:click="stopScan">stopScan</button>
    </div>
    <canvas id="canvas"> </canvas>
    <div class="output">
      <img id="photo" alt="The screen capture will appear in this box." />
    </div>
    <p>
      Visit our article
      <a
        href="https://developer.mozilla.org/en-US/docs/Web/API/WebRTC_API/Taking_still_photos"
      >
        Taking still photos with WebRTC</a
      >
      to learn more about the technologies used here.
    </p>
    <div>{{ this.res }}</div>
  </div>
</template>

<script>
import QrScanner from "qr-scanner";

export default {
  data() {
    return {
      qrScanner: {},
      res: "empty",
    };
  },

  mounted() {
    this.qrScanner = new QrScanner(
      this.$refs.video,
      (result) => {
        this.res = result.data;
        console.log("decoded qr code:", result);
      },
      {
        /* your options or returnDetailedScanResult: true if you're not specifying any other options */
      }
    );
  },

  methods: {
    stopScan() {
      this.qrScanner.stop();
    },

    startScan() {
      this.qrScanner.start();

      console.log(this.$refs.video);
    },
  },
};
</script>

